import {useEffect,useCallback,useMemo, useState} from 'react';

const _apiKey = '?api_key=51d93e8a2a1a27a409fc155a8ece4e79';
const _baseUrl = 'https://api.themoviedb.org/3/';
const _ruLang = '&language=ru-RU';
const _noAdult = '&include_adult=false';
const posterpath = 'https://image.tmdb.org/t/p/w500';
const backdroppathWorst = 'https://image.tmdb.org/t/p/w1066_and_h600_bestv2';
const backdroppathOriginal = 'https://image.tmdb.org/t/p/original';


const useRequest = (request) =>{
    const initialState = useMemo(()=>({
        data: null,
        loading: true,
        error: null
    }),[]);

    const [ dataState , setDataState ] = useState(initialState);

    useEffect(()=>{
        setDataState(initialState);
        let cancelled = false;
        request()
            .then(data=>!cancelled && setDataState({
                data: _noActor(data.results).map(_transfromMulti)
                        .slice(0, 3), //transfrom_data
                loading:false,
                error:null
            }))
            .catch(error=>!cancelled && setDataState({
                data: null,
                loading: false,
                error
            }));
        return () => cancelled = true;
    },[request, initialState]);

    return dataState;
};

const searchMulti = (query) =>{
    return fetch(`${_baseUrl}search/multi${_apiKey}${_ruLang}&query=${query}&page=1${_noAdult}`)
        .then(res=>res.json())
        .then(data=>data);
};

const findPopular = () =>{
    return fetch(`${_baseUrl}trending/tv/week${_apiKey}${_ruLang}`)
        .then(res=>res.json())
        .then(data=>data);
};

const useSearchMulti = (query) =>{
    const request = useCallback(
        ()=>searchMulti(query),[query]);
    return useRequest(request);
};

const useFindPopular = () =>{
    const request = useCallback(
        ()=>findPopular(),[]);
    return useRequest(request);
};

const _transfromMulti = (search) =>{
    return{
        id: search.id,
        name: search.name,
        title: search.title,
        poster: `${posterpath}${search.poster_path}`,
        backdrop: `${backdroppathWorst}${search.backdrop_path}`,
        backdropOrigianl: `${backdroppathOriginal}${search.backdrop_path}`,
        description: search.overview,
        releaseDate: search.release_date,
        originalName: search.original_name,
        originalTitle: search.original_title
    }
};

const _noActor = (object) =>{
    return object.filter((el)=>{
        return el.known_for_department === undefined;
    });
};

export{
    useSearchMulti,
    useFindPopular
};