import React from 'react';
import './app.scss';
import Search from '../search/';
import {TheMovieDBProvider} from '../context/themoviedb-context';
import * as TheMovieDB from '../../services/themoviedb';
import {LocalStorageProvider} from '../context/localstorage-context';
import * as LocalStorage from '../../services/localStorage';


const App = () =>{
    return(
        <React.Fragment>
            <TheMovieDBProvider value={TheMovieDB}>
                <LocalStorageProvider value={LocalStorage}>
                    <Search/>
                </LocalStorageProvider>
            </TheMovieDBProvider>
        </React.Fragment>
    );
};


export default App;