import React,{useContext} from 'react';
import TheMovieDBContext from '../context/themoviedb-context';
import LocalStorageContext from '../context/localstorage-context';
import Spinner from '../spinner';
import './search-results.scss';


const SearchResults = ({input,openPopup}) =>{
    const service = useContext(TheMovieDBContext);
    const search = service.useSearchMulti(input);
    const popular = service.useFindPopular([]);
    const inputl = (input.length > 2);

    const localService = useContext(LocalStorageContext);
    const localStorageData = localService.getLatest();

    if(inputl){
        return(
            SearchContainer(
                <ListOfMovies data={search} openPopup={openPopup}/>
            )
        );
    };
    return(
        <>
        {
        (localStorageData.data.length > 0) ? SearchContainer(
            <ListOfMovies data={localStorageData} openPopup={openPopup}/>,
            <h1>Недавние</h1>
        )   : false
        }
        {
        SearchContainer(
            <ListOfMovies data={popular} openPopup={openPopup}/>,
            <h1>Популярное</h1>
        )
        }
        </>
    )
};

const ListOfMovies = ({data,openPopup}) => {
    const localService = useContext(LocalStorageContext);
    
    if(data.error){
        console.log(`${data.error}`);
    };
    if(data.loading){
        return SearchContainer(<Spinner/>);
    };
    return(
        <div className="row mt-4">
            {                
            data.data.map((e)=>(
                <div key={e.id} className="col-md-4">
                    <div className="card mb-4 shadow-sm">
                        <img alt="Poster" src={`${e.poster}`} onClick={ 
                            () => {openPopup(e);localService.addLatest(e)}}/>
                    </div>
                    <p>{e.name || e.title}</p>
                </div>
            ))
            }
        </div>
    )
};

const SearchContainer = (View,head = null) =>{
    return(
        <div>
            <div className="container pt-3">
                {head}
                {View}
            </div>
        </div>
    )
}

export default SearchResults;