import * as TheMovieDB from './themoviedb';

const addLatest = (item) =>{
    let latest = localStorage.getItem('Latest');
    const ii = item.name || item.title;
    if(latest !== null){
        const ll = `${ii}[[[${latest}`;
        latest = [...new Set(ll.split('[[['))].slice(0,3).join('[[[');
        localStorage.setItem('Latest', latest);
    }else{
        localStorage.setItem('Latest', `${ii}`);
    };
};

const getLatest = () =>{
    const latest = localStorage.getItem('Latest');
    let ll = {data:[],loading:true,error:false};
    if(latest !== null){
        latest.split('[[[',3).map((e)=>{
            const dd = TheMovieDB.useSearchMulti(e);
            if(dd.data){
                if(e !== (dd.data[0].title || dd.data[0].name)){
                    dd.data.forEach((el,i) => {
                        if(e === el.title || el.name){
                            ll.data.push(dd.data[i]);
                        };
                    });
                }else{
                    ll.data.push(dd.data[0]);
                };
                ll.loading = false;
            }else if(dd.error){
                ll.error = dd.error;
                ll.loading = false;
            };
            return ll;
        });
    }else{
        ll.loading = false;
    };
    return ll;
};

export{
    addLatest,
    getLatest
};