import React,{Suspense} from 'react';
import {getVideo} from '../../services/hdrezka';
import {PlayerLoading} from '../loading';

const Player = ({data}) =>{
    const video = getVideo(data);
    if(data.movie){
        return <Video url={data.url}/>
    }
    return(
        <Suspense fallback={<PlayerLoading/>}>
            <Video promise={video}/>
        </Suspense>
    );
}

const Video = ({promise,url}) =>{
    const resource = promise.read();
    return(
        <div className='playerjs'>
            <iframe 
            title='playerjs'
            allowFullscreen='true'
            className='player fade-in'
            src={'playerjs.html?file='+(resource.url || url)}
            type='text/html'
            allowtransparency='true'
            />
        </div>
    );
}
export default Player;