import React,{useState} from 'react';
import {MainView} from '../resources';
import './popup.scss';

const Popup = ({selected,closePopup}) =>{
    return(
        <React.Fragment>
        <div className='backdrop'>
            <img alt='backdrop' src={selected.backdropOrigianl}/>
        </div>
        <div className='popup-info font-weight-light text-light shadow-sm'>
            <span className='back-span text-opacity p-4' onClick={()=>closePopup()}>X</span>
            <div className='blur'></div> 
            <div className='backdrop-content p-4 text-opacity'>
                <h1>{selected.name}</h1>
                <p className='description'>{selected.description}</p>
            </div>
        </div>
        <div className='resources p-4'>
            <h4 className='text-light text-opacity mb-4'>Сервис</h4>
            <Resources selected={selected}/>
        </div>
        </React.Fragment>
    );
};

const Resources = ({selected,handleVideo}) =>{
    const[res,setRes] = useState();
    const handleRes = (value) => {
        setRes(value);
    };
    const ress=[
        {value: 'hdrezka', name: 'HD REZKA', id: 'rr0', className: 'hdrezka-service'},
        {value: 'gidonline', name: 'GID ONLINE', id: 'rr1', className: 'gidonline-service'},
        //{value: 'filmix', name: 'FILMIX', id: 'rr2', className: 'filmix-service'}
    ];
    const Switcher = () =>{
        switch(res){
        case 'hdrezka':
            return <MainView res={res} selected={selected} handleVideo={handleVideo}/>;
        case 'gidonline':
            return false;
        case 'filmix':
            return false;
        default:
            return null;
        };
    };

    return(
        <React.Fragment>
        <div className='flex'>
        {
            ress.map(e=>
            <button className={`service shadow font-weight-bold text-light mr-3 ${e.className}`} 
            key={e.id} onClick={()=>handleRes(e.value)}>
                <span className='service-p text-opacity'>{e.name}</span>
            </button>
            )
        }
        </div>
        <Switcher/>
        </React.Fragment>
    );
};

export default Popup;